import bar from '../../components/bar/bar.vue'
import refresh from '../../components/refresh/refresh.vue'
export default {
	components: {
		bar,
		refresh
	},
	data() {
		return {
			testList: [
				{
					id: 'one',
					show: true
				},
				{
					id: 'two',
					show: false
				}
			],
			// 自定义导航栏对象
			setNav: {
				bg: '#FFFFFF', // 背景色
				navTitle: '评论', // 导航标题
				leftIcon: 'url(../../static/images/nav_left.png)', // 左图标
				// 'rightIcon': 'url(../../static/images/nav_right.png)',	// 右图标
				// 'rightText': '发布',										// 右文本
				isdisPlayNavTitle: true, // 是否显示返回按钮
				isRightType: 0 // 右侧标签显示类型 0不显示 1图标 2文本
				// 由于导航栏是共用的，把所有的东西封装好，然后有些页面不需要的东西通过条件控制进行显示与隐藏
			},
			inpType: 1
		}
	},
	// 页面上拉触底事件的处理函数
	onReachBottom: function(e) {
		console.log(e)
	},
	methods: {
		rightButton: function(e) {
			console.log(e) //子组件传递的参数
		},

		// 刷新touch监听
		refreshStart(e) {
			this.$refs.refresh.refreshStart(e)
		},
		refreshMove(e) {
			this.$refs.refresh.refreshMove(e)
		},
		refreshEnd(e) {
			this.$refs.refresh.refreshEnd(e)
		},
		isRefresh() {
			setTimeout(() => {
				uni.showToast({
					icon: 'success',
					mask: true, // 显示透明蒙层，防止触摸穿透
					title: '刷新成功'
				})
				this.$refs.refresh.endAfter() //刷新结束调用
			}, 1000)
		},
		// 回复
		reply: function(item) {
			// 拿到点击的时候元素的item
			console.log(item.id)
			let flag = item.show
			// 用ES2015的数组遍历方法，
			this.testList.map(function(ele) {
				console.log('ele', ele)
				console.log('item', item)
				// 判断遍历中的ele的id与点击时选中的id是否相等，相等则让ele的取反show
				if (ele.id === item.id) {
					ele.show = !flag
				}
			})
		},
		// 详情页
		openDetail: function(e) {
			uni.navigateTo({
				url: '/pages/detail/detail'
			})
		},
		// 用户中心
		userCenter: function() {
			uni.navigateTo({
				url: '/pages/user/user'
			})
		}
	}
}
